<template>
  <div class="search-wrap">
    <van-field
      v-model="inputValue"
      class="search-input"
      :placeholder="$t('dao.search')"
    />
    <div class="search-icon" @click="handleSearch">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#Sousuo"></use>
      </svg>
    </div>
  </div>
</template>

<script>
export default {
  name: '',
  props: ['value'],
  data () {
    return {

    }
  },
  computed: {
    inputValue: {
      get () {
        return this.value
      },
      set (val) {
        this.$emit('input', val)
      }
    }
  },
  methods: {
    handleSearch () {
      this.$emit('handleSearch')
    }
  },
  mounted () {

  }
}
</script>

<style scoped lang="scss">
.search-wrap {
  display: flex;
  .search-input {
    flex: 1;
    height: 36px;
    line-height: 35px;
    padding-left: 15px;
    background: #ffffff;
    box-shadow: 0px 4px 15px 0px rgba(0, 0, 0, 0.05);
    border-radius: 8px 0px 0px 8px;
  }
  .search-icon {
    margin-left: 5px;
    width: 51px;
    height: 36px;
    background: #ffffff;
    box-shadow: 0px 4px 15px 0px rgba(0, 0, 0, 0.05);
    border-radius: 0px 8px 8px 0px;
    color: #dbd9d5;
    font-size: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    .icon {
      width: 20px;
      height: 20px;
    }
  }
}
</style>
